/*Inicia Menu*/
.menu {
    height: 100%;
    width: 200px;
    display: block;
    position: fixed;
    color: #101C24;
    /*#1C0403;*/
    /*#B19C22;*/
    /* color: #DBD9E3; */

    
}

a {
    /* transition: 0.2s color linear; */
    /* color:rgb(248, 222, 222); */
    overflow: hidden;
}

a:hover {
    color: #F7DEDD;
}

a:active, a:focus, a:hover, a:link {
    text-decoration: none;
    background-color: transparent;
    cursor: pointer; 
}

.menu-link {
    /* font-family: 'Dosis', sans-serif; */
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    font-style: italic;
    /* color: black; */
    color: #101C24;
    /*#C7179E;*/
    /*#9A440C, #5717C7;*/
    width: 100%;
    font-size: 1.2em;
    
    
}

.menu-name {
    font-size: 1em;
}
/*Termina Menu*/

