.App {
  animation: movimiento 30s infinite linear alternate; 
  background-image: url(../src/components/Images/tapiz-personal.png);
  background-size: 130%;
  position: relative;
  text-align: center;
  width: 100%;
  height:100%;

}

@keyframes movimiento {
  from{
    background-position: bottom left;
  } to {
    background-position: top right;
  }
} 


.capa-gradiente {
  width: 100%;
  height: 100%;
  position: absolute;
  background:linear-gradient(to left top, #031B1C, #7BC8E1);
  opacity: 0.7;
}

.contenido-details{
  width: 100%;
  position: relative;
}

.Contenido {
  width: 100%;
  position: relative;
}


